import React, { useState } from "react";
import {
  Box,
  Container,
  Typography,
  Divider,
  Dialog,
  DialogTitle,
  DialogContent,
  IconButton,
  Button,
  Stack,
} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";

import RefundPolicy from "../../pages/refundPolicy";
import PrivacyPolicy from "../../pages/privacyPolicy";
import TermsAndConditions from "../../pages/termsAndConditions";

const Footer = () => {
  const policyLinks = [
    { title: "Privacy Policy", component: <PrivacyPolicy /> },
    { title: "Terms & Conditions", component: <TermsAndConditions /> },
    { title: "Refund Policy", component: <RefundPolicy /> },
  ];

  const [dialogTitle, setDialogTitle] = useState("");
  const [dialogContent, setDialogContent] = useState(null);
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  const openDialog = (title, content) => {
    setDialogTitle(title);
    setDialogContent(content);
    setIsDialogOpen(true);
  };

  const closeDialog = () => {
    setIsDialogOpen(false);
  };

  return (
    <Box
      component="footer"
      sx={{
        backgroundColor: "#0a2a4d",
        color: "white",
        py: 6,
      }}
    >
      <Container maxWidth="lg">
        <Divider sx={{ mb: 3, borderColor: "rgba(255, 255, 255, 0.3)" }} />

        <Box textAlign="center">
          <Stack
            direction="row"
            spacing={2}
            justifyContent="center"
            alignItems="center"
            flexWrap="wrap"
            sx={{ mb: 1 }}
          >
            {policyLinks.map((policy) => (
              <Button
                key={policy.title}
                onClick={() => openDialog(policy.title, policy.component)}
                sx={{
                  color: "rgba(255, 255, 255, 0.7)",
                  fontSize: "13px",
                  textTransform: "none",
                  px: 1,
                  "&:hover": {
                    color: "#fff",
                    textDecoration: "underline",
                  },
                }}
              >
                {policy.title}
              </Button>
            ))}
            <Typography
              variant="body2"
              sx={{
                color: "rgba(255, 255, 255, 0.6)",
                fontSize: { xs: "12px", sm: "13px" },
              }}
            >
              © {new Date().getFullYear()} Smintech. All rights reserved.
            </Typography>
          </Stack>
        </Box>
      </Container>

      {/* Dialog for policy content */}
      <Dialog
        open={isDialogOpen}
        onClose={closeDialog}
        fullWidth
        maxWidth="sm"
        TransitionProps={{
          onExited: () => {
            setDialogContent(null);
            setDialogTitle("");
          },
        }}
      >
        <DialogTitle
          sx={{
            backgroundColor: "#0a2a4d",
            color: "white",
            fontWeight: "bold",
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
          }}
        >
          {dialogTitle}
          <IconButton onClick={closeDialog} sx={{ color: "white" }}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        <DialogContent
          dividers
          sx={{
            backgroundColor: "#f7f7f7",
            color: "#333",
            padding: "24px",
          }}
        >
          {dialogContent}
        </DialogContent>
      </Dialog>
    </Box>
  );
};

export default Footer;
